<template>
  <div id="container">
    <h1> customJIBsnake Score: {{score}} </h1>
    <button v-on:click="gameLoop()" > Start Game </button>
    <button v-on:click="stopLoop()">Stop Game </button>
    <button v-on:click="killSnake()">KYS </button>
    <v-stage ref="stage"
      :config="configKonva">
      <v-layer ref="layer" :config="configMain">
        <v-rect :config="backgroundConfig"/>
        <v-rect :config="{
          x: foodX,
          y: foodY,
          width: 20,
          height: 20,
          fill: 'blue'
        }"/>
        <v-rect v-for="location in snakeBody" :key="location.id"  :config="{
            x: location.x,
            y: location.y,
            width: 20,
            height: 20,
            fill: 'red'
          }"
        />

      </v-layer>
      <v-layer ref="dragLayer"></v-layer>
    </v-stage>
  </div>
</template>

<script>
var width = 1400;
var height = 500;
let vm = {};
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data() {
    return {
      lastKey: '',
      score: 0,
      currentlyMoving: false,
      foodY: Math.round((Math.random() * (height - 0 - 10) + 0) / 25) * 25,
      foodX: Math.round((Math.random() * (width - 0 - 10) + 0) / 25) * 25,
      snakeBody: [
        {x: 225, y: 150},
        {x: 200, y: 150},
        {x: 175, y: 150},
        {x: 150, y: 150},
        {x: 125, y: 150},
        {x: 100, y: 150},
        {x: 75, y: 150},
        {x: 50, y: 150},
        {x: 25, y: 150}
      ],
      headOff: "",
      colors: ['red', 'blue', 'green'],
      xAxis: 25,
      yAxis: 0,
      backgroundConfig: {
        width: width,
        height: height,
        fill: 'black'
      },
      configKonva: {
        width: width,
        height: height,
        fill: 'black'
      },
      configMain: {
        fill: 'green'
      },
      configRect: {
        draggable: true,
        startScale: 10,
        width: 20,
        height: 20,
        fill: 'red',
      }
    }
  },
  methods: {
    gameLoop(){
      const stage = vm.$refs.stage.getNode();
        this.t = setInterval(() => {
          this.snakePush(),
          stage.draw();
          this.currentlyMoving = false
        }, 100)
    },
    stopLoop(){
      clearInterval(this.t)
    },
    checkSnakeCollide(head){
      return this.snakeBody.filter(obj => obj.x === head.x && obj.y === head.y)
    },
    findIndex(head){
      return this.snakeBody.findIndex(obj => obj.x === head.x && obj.y === head.y)
    },
    snakePush(){
      const head = {x: this.snakeBody[0].x + this.xAxis, y: this.snakeBody[0].y + this.yAxis};
      // Add the new head to the beginning of snake body
      this.snakeBody.unshift(head);

      this.headOff = this.snakeBody.shift()
      console.log(this.snakeBody.length);
      if (this.checkSnakeCollide(this.headOff).length) {
        console.log(this.findIndex(this.headOff))
        this.snakeBody.splice(this.findIndex(this.headOff), this.snakeBody.length + 1)
      }
      this.snakeBody.unshift(this.headOff)
      if (this.snakeBody[0].x === this.foodX && this.snakeBody[0].y === this.foodY) {
        this.foodY = Math.round((Math.random() * (height - 0 - 10) + 0) / 25) * 25
        this.foodX = Math.round((Math.random() * (width - 0 - 10) + 0) / 25) * 25
        this.score += 1
      }else {
        this.snakeBody.pop()
      }
    },
    killSnake(){
      console.log(this.findIndex(this.headOff))
      this.snakeBody.splice(this.findIndex(this.headOff), this.snakeBody.length)
    },
    updateCanvas(){
      const stage = vm.$refs.stage.getNode();
      stage.draw();
    },
  },
  created() {
    window.addEventListener('keydown', (e) => {
      if (this.currentlyMoving) return
      if (e.key == 'ArrowRight' && this.xAxis != -25) {
        this.xAxis = 25
        this.yAxis = 0
      }else if (e.key == 'ArrowLeft' && this.xAxis != 25) {
        this.xAxis = -25
        this.yAxis = 0
      }else if (e.key == 'ArrowUp' && this.yAxis != 25) {
        this.yAxis = -25
        this.xAxis = 0
      }else if (e.key == 'ArrowDown' && this.yAxis != -25) {
        this.yAxis = 25
        this.xAxis = 0
      }
      this.currentlyMoving = true
    });
  },
  mounted() {
    vm = this
  }

}
</script>

